'use client';

import { createContext, useContext } from 'react';

import { YaMusicAnalytics } from '@yandex-music-int/analytics';

export const AnalyticsContext = createContext<YaMusicAnalytics | null>(null);

export function useAnalytics(): YaMusicAnalytics | null {
    return useContext<YaMusicAnalytics | null>(AnalyticsContext);
}
