import { useMemo } from 'react';

import { useAnalyticsBlock } from '../providers/block/useAnalyticsBlock.js';
import { useAnalyticsPage } from '../providers/page/useAnalyticsPage.js';

const SEPARATOR = '-';

export const useCreateFrom = (params?: { pageId?: string; blockId?: string }): string => {
    const pageId = params?.pageId;
    const blockId = params?.blockId;

    const { pageId: pageIdFromProvider } = useAnalyticsPage();
    const { blockId: blockIdFromProvider } = useAnalyticsBlock();

    return useMemo(() => {
        const platfrom = process.env.BUILD_TYPE;
        const finalPageId = pageId ?? pageIdFromProvider;
        const finalBlockId = blockId ?? blockIdFromProvider;

        const fromParams: string[] = [platfrom];

        if (finalPageId) {
            fromParams.push(finalPageId);
        }

        if (finalBlockId) {
            fromParams.push(finalBlockId);
        }

        // По требованию аналитиков в конце параметра должен всегда приходить default
        fromParams.push('default');

        return fromParams.join(SEPARATOR);
    }, [blockId, blockIdFromProvider, pageId, pageIdFromProvider]);
};
