import {
    CONTAINER_APP_LANDING_RESOURCE_TOKEN,
    CONTAINER_APP_RESOURCE_TOKEN,
    CONTAINER_APP_USERS_RESOURCE_TOKEN,
    CONTAINER_APP_ALBUM_RESOURCE_TOKEN,
    CONTAINER_APP_LIBRARY_RESOURCE_TOKEN,
    CONTAINER_APP_TRACKS_RESOURCE_TOKEN,
    CONTAINER_APP_TOP_RESOURCE_TOKEN,
    CONTAINER_APP_ARTISTS_RESOURCE_TOKEN,
    CONTAINER_APP_SLIDES_RESOURCE_TOKEN,
    CONTAINER_APP_ROTOR_RESOURCE_TOKEN,
    CONTAINER_APP_SEARCH_RESOURCE,
    CONTAINER_APP_PLAYLIST_RESOURCE_TOKEN
} from '../container/constants/tokens.js';
import { useContainer } from '../container/useContainer.js';

import { Resources } from './types/Resources.js';

export const useResources = (): Resources => {
    const container = useContainer();
    const accountResource = container.get(CONTAINER_APP_RESOURCE_TOKEN);
    const usersResource = container.get(CONTAINER_APP_USERS_RESOURCE_TOKEN);
    const landingResource = container.get(CONTAINER_APP_LANDING_RESOURCE_TOKEN);
    const albumResource = container.get(CONTAINER_APP_ALBUM_RESOURCE_TOKEN);
    const libraryResource = container.get(CONTAINER_APP_LIBRARY_RESOURCE_TOKEN);
    const tracksResource = container.get(CONTAINER_APP_TRACKS_RESOURCE_TOKEN);
    const topResource = container.get(CONTAINER_APP_TOP_RESOURCE_TOKEN);
    const artistsResource = container.get(CONTAINER_APP_ARTISTS_RESOURCE_TOKEN);
    const slidesResource = container.get(CONTAINER_APP_SLIDES_RESOURCE_TOKEN);
    const rotorResource = container.get(CONTAINER_APP_ROTOR_RESOURCE_TOKEN);
    const searchResource = container.get(CONTAINER_APP_SEARCH_RESOURCE);
    const playlistResource = container.get(CONTAINER_APP_PLAYLIST_RESOURCE_TOKEN);

    return {
        accountResource,
        usersResource,
        landingResource,
        albumResource,
        libraryResource,
        tracksResource,
        topResource,
        artistsResource,
        slidesResource,
        rotorResource,
        searchResource,
        playlistResource
    };
};
