'use client';

import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '../../../lib/types/WithClassName.js';
import { BaseNotification } from '../BaseNotification.js';

import styles from './BaseNotificationError.module.scss';

interface Props extends WithClassName {
    error: string;
    closeToast?: () => void;
}

export const BaseNotificationError = ({ error, closeToast, className }: Props): ReactElement => {
    const message = useMemo(() => {
        return (
            <Caption className={styles.message} variant="div" type="controls" size="m">
                {error}
            </Caption>
        );
    }, [error]);

    return <BaseNotification className={clsx(styles.root, className)} message={message} onClose={closeToast} />;
};
