'use client';

import { createContext, useContext } from 'react';

import { BaseException } from '@yandex-music-int/exception';

import { getContainer } from './getContainer.js';

export const ContainerContext = createContext<Awaited<ReturnType<typeof getContainer>> | null>(null);

export function useContainer(): Awaited<ReturnType<typeof getContainer>> {
    const container = useContext<Awaited<ReturnType<typeof getContainer>> | null>(ContainerContext);

    if (container === null) {
        throw new BaseException('Container cannot be null, please add a context provider', {
            code: 'E_CONTEXT_CONTAINER_NULL'
        });
    }

    return container;
}
