'use client';

import { createContext, useContext } from 'react';

import { HistoryNode } from './HistoryNode.js';
import { HistoryNodeData } from './types/HistoryNodeData.js';

interface HistoryContextType {
    pushState: (data: HistoryNodeData) => void;
    replaceState: (data: HistoryNodeData) => void;
    forward: () => void;
    back: () => void;
    canForward: boolean;
    canBack: boolean;
    state: HistoryNode | null;
}

export const HistoryContext = createContext<HistoryContextType>({
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    pushState: () => {},
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    replaceState: () => {},
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    forward: () => {},
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    back: () => {},
    canForward: false,
    canBack: false,
    state: null
});

export function useHistory(): HistoryContextType {
    return useContext<HistoryContextType>(HistoryContext);
}
