'use client';

import { isEqual } from 'lodash-es';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useIntl } from 'react-intl';

import { isVibeContext as checkIsVibeContext, VibeContext, YaMusicContexts } from '@yandex-music-int/player-contexts';
import { AudioPlayerStatus } from '@yandex-music-int/sonata';

import { BaseNotificationError } from '../../ui/BaseNotification/BaseNotificationError/BaseNotificationError.js';
import { useCreateFrom } from '../analytics/froms/useCreateFrom.js';
import { NotificationContainerId } from '../notification/types/NotificationContainerId.js';
import { useNotify } from '../notification/useNotify.js';
import { useStore } from '../store/useStore.js';
import { DEFAULT_VIBE_STATION_ID } from '../vibe/constants.js';

import { useSonata } from './useSonata.js';

interface Props {
    seeds: string[];
    // Параметры для фромов, которые зависят от контекста старта Моей волны
    // Подробнее можно посмотреть тут https://st.yandex-team.ru/MFRONT-6004#6556207d7aa7ce4f591ef3a1
    pageIdForFrom: string | undefined;
    blockIdForFrom: string | undefined;
}

interface PlayableVibeContext {
    isVibeContext: boolean;
    isPlaying: boolean;
    togglePlay: () => void;
    applySetting: (seed: string, isStation?: boolean) => Promise<void>;
    resetSettings: () => Promise<void>;
}

export const usePlayableVibeContext = ({
    seeds = [DEFAULT_VIBE_STATION_ID],
    pageIdForFrom,
    blockIdForFrom
}: Props): PlayableVibeContext => {
    const from = useCreateFrom({ pageId: pageIdForFrom, blockId: blockIdForFrom });
    const { formatMessage } = useIntl();
    const sonata = useSonata();
    const { notify } = useNotify();
    const { sonataState, vibe } = useStore();

    const [vibeContext, setVibeContext] = useState<VibeContext | null>(null);

    useEffect(() => {
        const unsubscribeFromContextChange = sonata?.state.currentContext.onChange(() => {
            if (checkIsVibeContext(sonata.state.currentContext.value)) {
                setVibeContext(sonata.state.currentContext.value);
            } else {
                setVibeContext(null);
            }
        });

        return () => {
            unsubscribeFromContextChange?.();
        };
    }, [sonata]);

    const playVibeContext = useCallback(
        async (seedsForPlay: string[]) => {
            try {
                await sonata?.playContext({
                    contextData: {
                        type: YaMusicContexts.Vibe,
                        meta: {
                            id: YaMusicContexts.Vibe
                        },
                        seeds: seedsForPlay,
                        from,
                        includeTracksInResponse: true
                    },
                    loadContextMeta: true
                });
            } catch (error) {
                notify(<BaseNotificationError error={formatMessage({ id: 'vibe-errors.start-vibe' })} />, {
                    containerId: NotificationContainerId.ERROR
                });
            }
        },
        [sonata, from, formatMessage, notify]
    );

    const setSettings = useCallback(
        async (settings: string[]) => {
            if (sonata && vibeContext) {
                try {
                    await vibeContext.setSettings(settings, sonata.state.playerState, sonata.state.queueState);
                } catch (error) {
                    notify(<BaseNotificationError error={formatMessage({ id: 'vibe-errors.apply-vibe-setting' })} />, {
                        containerId: NotificationContainerId.ERROR
                    });
                }
            }
        },
        [formatMessage, notify, sonata, vibeContext]
    );

    const restartContext = useCallback(
        async (playAfterRestart: boolean) => {
            if (sonata) {
                try {
                    await sonata.restartContext({
                        playAfterRestart
                    });
                } catch (error) {
                    notify(
                        <BaseNotificationError error={formatMessage({ id: 'error-messages.something-went-wrong' })} />,
                        {
                            containerId: NotificationContainerId.ERROR
                        }
                    );
                }
            }
        },
        [formatMessage, notify, sonata]
    );

    const isVibeContext = Boolean(vibeContext);

    useEffect(() => {
        if (isVibeContext && vibeContext?.data.meta.session?.wave) {
            vibe.setVibe(vibeContext?.data.meta.session?.wave);
        }
    }, [isVibeContext, vibeContext?.data.meta.session?.wave, vibe]);

    const isPlaying = useMemo(() => {
        if (isVibeContext) {
            if (isEqual(vibe.meta?.seeds, seeds) && sonataState.status === AudioPlayerStatus.PLAYING) {
                return true;
            }
        }
        return false;
    }, [vibe.meta?.seeds, isVibeContext, seeds, sonataState.status]);

    const togglePlay = useCallback(async (): Promise<void> => {
        vibe.setIsLoading(true);
        await playVibeContext(seeds);
        vibe.setIsLoading(false);
    }, [playVibeContext, seeds, vibe]);

    const applySetting = useCallback(
        async (newSeed: string, isStation?: boolean) => {
            vibe.setIsLoading(true);
            let seedsForPlay = [];
            if (vibe.meta && Array.isArray(vibe.meta?.seeds)) {
                if (vibe.meta.seeds.includes(newSeed)) {
                    seedsForPlay = vibe.meta.seeds.filter((seed: string) => seed !== newSeed);
                } else {
                    if (isStation) {
                        seedsForPlay = [newSeed, ...vibe.meta.seeds.filter((seed: string) => seed.includes('setting'))];
                    } else {
                        seedsForPlay = [...vibe.meta.seeds, newSeed];
                    }
                }
            } else {
                seedsForPlay = [newSeed];
            }

            if (vibeContext) {
                await setSettings(seedsForPlay);
                await restartContext(true);
            } else {
                await playVibeContext(seedsForPlay);
            }
            vibe.setIsLoading(false);
        },
        [restartContext, setSettings, playVibeContext, vibe, vibeContext]
    );

    const resetSettings = useCallback(async () => {
        vibe.setIsLoading(true);
        if (vibeContext) {
            await setSettings([DEFAULT_VIBE_STATION_ID]);
            await restartContext(true);
        } else {
            await playVibeContext([DEFAULT_VIBE_STATION_ID]);
        }
        vibe.setIsLoading(false);
    }, [playVibeContext, restartContext, setSettings, vibeContext, vibe]);

    return {
        isVibeContext,
        isPlaying,
        togglePlay,
        applySetting,
        resetSettings
    };
};
