import { areDatesEqual } from './areDatesEqual.js';

export enum DateType {
    TODAY = 'today',
    YESTERDAY = 'yesterday',
    DATE = 'date',
    DATE_WITH_YEAR = 'date-with-year'
}

export const getDateType = (date: Date): DateType => {
    const today = new Date();

    if (areDatesEqual(today, date)) {
        return DateType.TODAY;
    }

    const yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);

    if (areDatesEqual(yesterday, date)) {
        return DateType.YESTERDAY;
    }

    if (today.getFullYear() !== date.getFullYear()) {
        return DateType.DATE_WITH_YEAR;
    }

    return DateType.DATE;
};
