import { HistoryNodeData } from './types/HistoryNodeData.js';

export class HistoryNode {
    public data: HistoryNodeData;
    public next: HistoryNode | null;
    public prev: HistoryNode | null;

    public constructor(data: HistoryNodeData, next: HistoryNode | null = null, prev: HistoryNode | null = null) {
        this.data = data;
        this.next = next;
        this.prev = prev;
    }

    public isLocationNotEqual(href: string): boolean {
        return this.data.href !== href;
    }

    public toString(): string {
        return JSON.stringify(this.data);
    }
}
