'use client';

import { createContext, useContext } from 'react';

import { BaseException } from '@yandex-music-int/exception';

export const StoreContext = createContext<RootModel | null>(null);

export function useStore(): RootModel {
    const store = useContext<RootModel | null>(StoreContext);

    if (store === null) {
        throw new BaseException('Store cannot be null, please add a context provider', {
            code: 'E_CONTEXT_STORE_NULL'
        });
    }

    return store;
}
