import { HttpClient } from '@yandex-music-int/http-client';
import { Logger } from '@yandex-music-int/logger';
import { TvmValue } from '@yandex-music-int/resource-collection';
import { ServerCookieStorage } from '@yandex-music-int/storage';

import { Storage } from '../types/Storage.js';
import { StorageKeys } from '../types/StorageKeys.js';

import { BlackboxError } from './types/BlackboxError.js';
import { BlackboxSession } from './types/BlackboxSession.js';

const isBlackboxError = (data: BlackboxSession | BlackboxError): data is BlackboxError => {
    return data.error !== 'OK';
};

export class Blackbox {
    protected userTicket?: string = undefined;
    protected userSession: BlackboxSession | undefined;

    public constructor(
        private httpClient: HttpClient,
        private logger: Logger,
        protected blackboxHost: string,
        protected serviceTicket: TvmValue | undefined,
        protected cookieStorage: ServerCookieStorage<Storage>
    ) {}

    public async loadSession(host: string, userip: string): Promise<void | undefined> {
        const sessionid = this.cookieStorage.get(StorageKeys.SessionId, false);

        if (sessionid && this.serviceTicket) {
            const response = await this.httpClient.post<BlackboxSession | BlackboxError>('blackbox', {
                prefixUrl: this.blackboxHost,
                headers: {
                    'X-Ya-Service-Ticket': this.serviceTicket.ticket
                },
                searchParams: new URLSearchParams({
                    sessionid,
                    method: 'sessionid',
                    host,
                    userip,
                    get_user_ticket: 'true',
                    format: 'json'
                })
            });

            const data = await response.json();
            if (isBlackboxError(data)) {
                this.logger.error(data.error);
                return;
            }

            this.userTicket = data.user_ticket;
            this.userSession = data;
        }
    }

    public getUserTicket(): string | undefined {
        return this.userTicket!;
    }

    public getUserSession(): BlackboxSession | undefined {
        return this.userSession;
    }
}
