import { ReactElement, useMemo } from 'react';

import { WithChildren } from '../../../types/WithChildren.js';

import { AnalyticsPageContext } from './AnalyticsPageContext.js';

interface Props extends WithChildren {
    pageId: string;
}

export const AnalyticsPageProvider = ({ pageId, children }: Props): ReactElement => {
    const value = useMemo(() => {
        return {
            pageId
        };
    }, [pageId]);

    return <AnalyticsPageContext.Provider value={value}>{children}</AnalyticsPageContext.Provider>;
};
