import { useCallback } from 'react';

import { notification, NotificationContent } from '@yandex-music-int/sol/components/Notification';

import { configs } from './configs.js';
import { NotificationContainerId } from './types/NotificationContainerId.js';

export interface NotifyOptions {
    containerId: NotificationContainerId;
}

export type NotifyFunction = (message: NotificationContent, options: NotifyOptions) => void;

interface UseNotifyReturn {
    notify: NotifyFunction;
}

export const useNotify = (): UseNotifyReturn => {
    const notify = useCallback((message: NotificationContent, { containerId }: NotifyOptions): void => {
        notification({
            message,
            options: {
                ...configs[containerId],
                containerId
            }
        });
    }, []);

    return {
        notify
    };
};
