'use client';

import { useCallback, useState } from 'react';

import { useLogger } from '../logger/useLogger.js';
import { MetrikaInitParams } from '../types/MetrikaInitParams.js';

interface UseMetrikaReturnType {
    isLoaded: boolean;
    init: (params: MetrikaInitParams) => void;
    count: (
        params: Record<string, unknown> | Array<Record<string, unknown>> | string,
        topLevelParameter: string
    ) => void;
    hit: (url: string) => void;
}

const DEFAULT_TOP_LEVEL = 'counter';
let singletonMetrika: Metrika | null = null;

export const useMetrika = (): UseMetrikaReturnType => {
    const logger = useLogger();
    const [isLoaded, setIsLoaded] = useState(singletonMetrika !== null);

    const init = useCallback(
        (params: MetrikaInitParams) => {
            if (typeof window === 'undefined' || typeof window.Ya?.Metrika2 === 'undefined' || isLoaded) {
                return;
            }

            try {
                singletonMetrika = new window.Ya.Metrika2({
                    ...params
                });
                setIsLoaded(true);
            } catch (error) {
                if (typeof error === 'string' || error instanceof Error) {
                    logger.error(error);
                }
            }
        },
        [logger, isLoaded]
    );

    const hit = useCallback((url: string) => {
        singletonMetrika?.hit(url);
    }, []);

    // TODO: сделать поддержку отложенной отправки событий, если метрика еще не загрузилась
    const count = useCallback(
        (
            params: Record<string, unknown> | Array<Record<string, unknown>> | string,
            topLevelParameter: string = DEFAULT_TOP_LEVEL
        ) => {
            singletonMetrika?.params({
                [topLevelParameter]: params
            });
        },
        []
    );

    return {
        isLoaded,
        init,
        count,
        hit
    };
};
