// Отключаем правила, так как из-за вложенности типов ts не может вывести конечный тип
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export async function getContainer(tld: string, env: typeof process.env.NEXT_PUBLIC_CONFIG_ENV): Promise<any> {
    if (process.env.BUILD_TYPE === 'landing') {
        const { createLandingContainer } = await import('./landingContainer.js');

        return createLandingContainer();
    }

    if (process.env.BUILD_TYPE === 'desktop') {
        const { createDesktopContainer } = await import('./desktopContainer.js');

        return createDesktopContainer(tld);
    }

    if (typeof window === 'undefined') {
        const { createServerContainer } = await import('./serverContainer.js');

        return createServerContainer(tld);
    }

    const { createClientContainer } = await import('./clientContainer.js');

    return createClientContainer(tld, env);
}
