interface URLPatternParams {
    [param: string]: string | number;
}

/**
 *
 * @param url – целевой урл из которого соберутся параметры для десктопа
 * @param pattern – паттерн по которому собираются параметры для десктопа
 * @returns – заполненый или пустой оъект с параметрами на основе url и паттерна
 */
export function extractParamsFromURL(url: string, pattern: string): URLPatternParams {
    const parts = url.split('/').filter((part: string) => part !== '');
    const result: URLPatternParams = {};

    const patternParts = pattern.split('/').filter((part: string) => part !== '');

    if (parts.length !== patternParts.length || (patternParts[0] && !url.startsWith(`/${patternParts[0]}`))) {
        return result;
    }

    for (let i = 0; i < patternParts.length; i++) {
        const patternPart = patternParts[i];
        const urlPart = parts[i];

        if (patternPart?.startsWith(':') && urlPart) {
            const paramName = patternPart.substring(1);
            result[paramName] = urlPart;
        }
    }

    return result;
}
