import { cast } from 'mobx-state-tree';

import { CaseForms as CaseFormsResponse } from '@yandex-music-int/resource-collection';

import { CaseFormsModel } from '../../models/CaseForms.js';

export const caseFormsResponseToModel = (response: CaseFormsResponse): CaseFormsModel => {
    return cast({
        nominative: response.nominative,
        genitive: response.genitive,
        dative: response.dative,
        accusative: response.accusative,
        instrumental: response.instrumental,
        prepositional: response.prepositional
    });
};
