'use client';

import { usePathname } from 'next/navigation.js';
import { useEffect, useState } from 'react';

const isDesktop = process.env.BUILD_TYPE === 'desktop';

/**
 * Хук для работы с Rum в десктопном приложении в режиме SPA.
 * @param isDataLoaded {boolean} флаг окнчания загрузки данных
 */
export const useRumPage = (isDataLoaded: boolean): void => {
    const pathName = usePathname();

    const [isCounterInited, setIsCounterInited] = useState(false);

    useEffect(() => {
        if (isDesktop) {
            // открытие страницы в режиме SPA
            window.Ya.Rum.spa.makeSpaSubPage(pathName);
            // для последней созданной подстраницы с заданным именем отправляет время начала загрузки данных
            window.Ya.Rum.spa.startDataLoading(pathName);
        }
    });

    useEffect(() => {
        if (isDesktop) {
            const hasLastSpaSubPage = Boolean(window.Ya.Rum.spa.getLastSpaSubPage(pathName));
            if (hasLastSpaSubPage && isDataLoaded && !isCounterInited) {
                // для последней созданной подстраницы с заданным именем отправляет время окончания загрузки данных
                window.Ya.Rum.spa.finishDataLoading(pathName);
                // для последней созданной подстраницы с заданным именем отправляет метрики рендеринга
                window.Ya.Rum.spa.startDataRendering(pathName);

                setIsCounterInited(true);
            }
        }
    }, [isDataLoaded, isCounterInited, pathName]);
};
