    WebNext = 'WebNext',
    /** Отключить показ версий альбома на странице альбома */
    WebNextAlbumDisableVersions = 'WebNextAlbumDisableVersions',
    /** Отключить показ треков на странице альбома */
    WebNextAlbumDisableTracks = 'WebNextAlbumDisableTracks',
    /** Отключить показ раздела Новые альбомы в этом жанре */
    WebNextAlbumDisableLatestGenreAlbums = 'WebNextAlbumDisableLatestGenreAlbums',
    /** Отключить показ раздела Другие альбомы исполнителя */
    WebNextAlbumDisableOtherArtistAlbums = 'WebNextAlbumDisableOtherArtistAlbums',
    /** Отключить показ последних релизов артиста */
    WebNextArtistDisableLastReleases = 'WebNextArtistDisableLastReleases',
    /** Отключить показ популярных треков артиста */
    WebNextArtistDisablePopularTracks = 'WebNextArtistDisablePopularTracks',
    /** Отключить показ раздела Популярные альбомы артиста */
    WebNextArtistDisablePopularAlbums = 'WebNextArtistDisablePopularAlbums',
    /** Отключить показ раздела Плейлисты артиста */
    WebNextArtistDisablePlaylists = 'WebNextArtistDisablePlaylists',
    /** Отключить показ раздела Похожие исполнители */
    WebNextArtistDisableSimilarArtists = 'WebNextArtistDisableSimilarArtists',
    /** Включение раздела лэнднга НГ 2023 */
    WebRewind2023 = 'WebRewind2023',
    /** Отключить раздел Коллекция */
    WebNextDisableCollection = 'WebNextDisableCollection',
    /** Отключить раздел Любимые артисты в Коллекции */
    WebNextDisableCollectionArtists = 'WebNextDisableCollectionArtists',
    /** Отключить раздел Любимые альбомы в Коллекции */
    WebNextDisableCollectionAlbums = 'WebNextDisableCollectionAlbums',
    /** Отключить раздел Мои плейлисты в Коллекции */
    WebNextDisableCollectionCreatedPlaylists = 'WebNextDisableCollectionCreatedPlaylists',
    /** Отключить раздел Любимые плейлисты в Коллекции */
    WebNextDisableCollectionLikedPlaylists = 'WebNextDisableCollectionLikedPlaylists',
    /** Отключить полностью музвитрину */
    WebNextDisableLanding = 'WebNextDisableLanding',
    /**  Отключить табы на витрине по id таба (массив id) */
    WebNextDisableSkeletonTabs = 'WebNextDisableSkeletonTabs',
    /**  Отключить блоки на витрине по id блока (массив id) */
    WebNextDisableSkeletonBlocks = 'WebNextDisableSkeletonBlocks',
    /**  Отключить Мою волну */
    WebNextDisableVibe = 'WebNextDisableVibe',
    /** Отключить настройки моей волны */
    WebNextDisableVibeSettings = 'WebNextDisableVibeSettings',
    /** Отключить раздел Поиск */
    WebNextDisableSearch = 'WebNextDisableSearch',
    /** Включить новое лого */
    // eslint-disable-next-line @typescript-eslint/naming-convention
    webLogo2023 = 'webLogo2023'
