import { ReactElement, useMemo } from 'react';

import { WithChildren } from '../../../types/WithChildren.js';

import { AnalyticsBlockContext } from './AnalyticsBlockContext.js';

interface Props extends WithChildren {
    blockId: string;
}

export const AnalyticsBlockProvider = ({ blockId, children }: Props): ReactElement => {
    const value = useMemo(() => {
        return {
            blockId
        };
    }, [blockId]);

    return <AnalyticsBlockContext.Provider value={value}>{children}</AnalyticsBlockContext.Provider>;
};
