import { useContext } from 'react';

import { SearchResourceClickType } from '@yandex-music-int/resource-collection';

import { SearchFeedbackContext } from './SearchFeedbackContext.js';
import { SearchFeedbackHookResult } from './types/SearchFeedbackHookResult.js';

export const useSearchFeedback = (): SearchFeedbackHookResult => {
    const { sendSearchFeedback, id, type, blockPosition, position } = useContext(SearchFeedbackContext) || {};

    // проверка на undefined, потому что какое-то из значений легально может быть нулем
    if (
        typeof id === 'undefined' ||
        typeof type === 'undefined' ||
        typeof blockPosition === 'undefined' ||
        typeof position === 'undefined'
    ) {
        return {};
    }

    const sendLikeSearchFeedback = sendSearchFeedback?.bind(null, {
        feedbackType: SearchResourceClickType.LIKE,
        id,
        type,
        blockPosition,
        position
    });

    const sendNavigateSearchFeedback = sendSearchFeedback?.bind(null, {
        feedbackType: SearchResourceClickType.NAVIGATE,
        id,
        type,
        blockPosition,
        position
    });

    const sendPlaySearchFeedback = sendSearchFeedback?.bind(null, {
        feedbackType: SearchResourceClickType.PLAY,
        id,
        type,
        blockPosition,
        position
    });

    return { sendLikeSearchFeedback, sendNavigateSearchFeedback, sendPlaySearchFeedback };
};
