import { DerivedColors } from '@yandex-music-int/resource-collection';

export const getSafeAverageColor = (derivedColors?: DerivedColors): string => {
    if (typeof derivedColors?.average === 'string') {
        return derivedColors?.average;
    } else if (
        typeof derivedColors?.average === 'object' &&
        // @ts-expect-error
        typeof derivedColors?.average?.color === 'string'
    ) {
        // @ts-expect-error
        return derivedColors?.average?.color;
    }
    return '';
};
