import { useMemo } from 'react';

import { buildUrl } from '../buildUrl.js';
import { extractParamsFromURL } from '../extractParamsFromURL.js';
import { useStore } from '../store/useStore.js';

import { SUPPORTED_PAGES } from './supportedPages.js';

interface UseSafePlatformLinkResult {
    href: string;
    target?: string;
    rel?: string;
}

export const useSafePlatformLink = (url: string): UseSafePlatformLinkResult => {
    const { settings } = useStore();

    return useMemo(() => {
        let isExternalLink = !url.startsWith('/');
        let finalLink = url;
        const [, pageFromPathname] = url.match(/\/([^\\/\\?]*)/) ?? [];

        if (pageFromPathname && !SUPPORTED_PAGES.includes(pageFromPathname)) {
            isExternalLink = true;
            finalLink = `https://music.yandex.${settings.clientTld}${url}`;
        } else {
            switch (true) {
                case url.startsWith('/users/'): {
                    const pattern = '/users/:userId/playlists/:kind';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                case url.startsWith('/album/'): {
                    const pattern = '/album/:albumId';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                case url.startsWith('/artist/'): {
                    const pattern = '/artist/:artistId';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                case url.startsWith('/playlist/'): {
                    const pattern = '/playlist/:playlistUuid';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                case url.startsWith('/rewind2023/artist'): {
                    const pattern = '/rewind2023/artist/:artistId';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                case url.startsWith('/slides/artist'): {
                    const pattern = '/slides/artist/:artistId';
                    finalLink = buildUrl(pattern, extractParamsFromURL(url, pattern));
                    break;
                }
                default:
                    break;
            }
        }

        return {
            href: finalLink,
            target: isExternalLink ? '_blank' : '_self',
            rel: isExternalLink ? 'noreferrer noopener' : ''
        };
    }, [url, settings.clientTld]);
};
