/**
 *
 * @param pattern – паттерн по которому составится url, к примеру /album/:albumId
 * @param params – параметры для подставления в url, к пример { albumId: 123 }
 * @returns – url, который в зависимости от платформы собран или в классическом варианте или с query params
 */
export function buildUrl(pattern: string, params: Record<string, string | number>): string {
    if (process.env.BUILD_TYPE === 'desktop') {
        const queryString = Object.entries(params)
            .map(([key, value]: [string, string | number]) => `${key}=${value}`)
            .join('&');

        const segments = pattern.split('/');
        const pathFromPatter = pattern.startsWith('/') ? segments[1] : segments[0];
        const basePath = pathFromPatter ?? pattern;
        const query = queryString ? `?${queryString}` : ``;
        return `/${basePath}${query}`;
    }

    let url = `${pattern}`;
    for (const [key, value] of Object.entries(params)) {
        url = url.replace(`:${key}`, value.toString());
    }
    return url;
}
