import { ClientSessionStorage } from '@yandex-music-int/storage';

import { Storage } from '../types/Storage.js';
import { StorageKeys } from '../types/StorageKeys.js';

import { HistoryNode } from './HistoryNode.js';
import { HistoryNodeData } from './types/HistoryNodeData.js';

export class HistoryList {
    private current: HistoryNode | null = null;

    public constructor(private readonly sessionStorage: ClientSessionStorage<Storage>) {
        this.restoreHistory();
    }

    public replaceState(data: HistoryNodeData): void {
        let node: HistoryNode = new HistoryNode(data, null, null);

        if (this.current) {
            node = new HistoryNode(data, this.current.next, this.current.prev);
        }

        this.current = node;

        this.updateSessionStorage();
    }

    public pushState(data: HistoryNodeData): void {
        let node: HistoryNode = new HistoryNode(data, null, null);

        if (this.current && this.current.isLocationNotEqual(data.href)) {
            node = new HistoryNode(data, null, this.current);
            this.current.next = node;
        }

        this.current = node;

        this.updateSessionStorage();
    }

    public get state(): HistoryNode | null {
        return this.current;
    }

    public get canForward(): boolean {
        return Boolean(this.current?.next);
    }

    public get canBack(): boolean {
        return Boolean(this.current?.prev);
    }

    public forward(): void {
        if (!this.canForward) {
            return;
        }

        const next = this.current?.next;

        if (next) {
            this.current = next;
        }

        this.updateSessionStorage();
    }

    public back(): void {
        if (!this.canBack) {
            return;
        }

        const prev = this.current?.prev;

        if (prev) {
            this.current = prev;
        }

        this.updateSessionStorage();
    }

    private updateSessionStorage(): void {
        if (this.current) {
            this.sessionStorage.set(StorageKeys.SessionHistoryState, this.current.data);
        }
    }

    private restoreHistory(): void {
        const historyData = this.sessionStorage.get<HistoryNodeData>(StorageKeys.SessionHistoryState);
        if (!historyData) {
            return;
        }

        this.replaceState(historyData);
    }
}
