import { clsx } from 'clsx';
import { ReactElement, ReactNode } from 'react';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Paper } from '@yandex-music-int/sol/components/Paper';

import { WithClassName } from '../../lib/types/WithClassName.js';

import styles from './BaseNotification.module.scss';

interface Props extends WithClassName {
    message: ReactNode;
    onClose?: () => void;
}

export const BaseNotification = ({ message, onClose, className }: Props): ReactElement => {
    return (
        <Paper radius="l" className={clsx(styles.root, className)}>
            <div className={styles.message}>{message}</div>
            {onClose && (
                <div className={styles.controls}>
                    <Button
                        variant="text"
                        color="secondary"
                        withRipple={false}
                        onClick={onClose}
                        icon={<Icon className={styles.icon} size="xs" variant="close" />}
                    />
                </div>
            )}
        </Paper>
    );
};
